import { ensureRegion } from "@/api/RegionProvider";
import { ApiHttpClient } from "@/globals";

export default{
    cache: {
        dropIndex:''
    },
    getDropIndex(okcb: any, errcb: any) {
        if (this.cache.dropIndex != '') {
            okcb(this.cache.dropIndex);
            return;
        }

        ApiHttpClient.get(`dropIndex.php?lan=cn`,
        {
        }).then(
        (res) => {
            this.cache.dropIndex = res.data;
            okcb(res.data);
        }, 
        errcb);
    },
}