<template>
    <div class="item-toolkit-index">
        <go-back />
        <div class="kit-list">
            <div v-if="loading">
                <load-indicator
                   loadText="正在加载中"></load-indicator>
            </div>
            <template v-if="routeName == 1">
                <div v-if="indexData">
                    <div v-for="(item,index) in indexData.Dungeon" :key="index">
                        {{item.name}}：
                        <span v-for="(v,i) in item.children" :key="i">
                            <template v-if="v.type==1">
                                <button  @click="ShowDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==2">
                                <button  class="gold-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==3">
                                <button  class="sliver-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==4">
                                <button  @click="Drops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                        </span>
                    </div>
                </div>
            </template>
            <template v-else-if="routeName == 2">
                <div v-if="indexData">
                    <div v-for="(item,index) in indexData.requestBoard" :key="index">
                        {{item.name}}：
