                            </template>
                            <template v-else-if="v.type==2">
                                <button  class="gold-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==3">
                                <button  class="sliver-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==4">
                                <button  @click="Drops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                        </span>
                    </div>
                </div>
            </template>
            <template v-else-if="routeName == 3">
                <div v-if="indexData">
                    <div v-for="(item,index) in indexData.week" :key="index">
                        {{item.name}}：
                        <span v-for="(v,i) in item.children" :key="i">
                            <template v-if="v.type==1">
                                <button  @click="ShowDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==2">
                                <button  class="gold-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==3">
                                <button  class="sliver-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==4">
                                <button  @click="Drops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                        </span>
                    </div>
                </div>
            </template>
            <template v-else-if="routeName == 4">
                <div v-if="indexData">
                    <div v-for="(item,index) in indexData.special" :key="index">
                        {{item.name}}：
                        <span v-for="(v,i) in item.children" :key="i">
                            <template v-if="v.type==1">
                                <button  @click="ShowDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==2">
                                <button  class="gold-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==3">
                                <button  class="sliver-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==4">
                                <button  @click="Drops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                        </span>
                    </div>
                </div>
            </template>
            <template v-else-if="routeName == 5">
                <div v-if="indexData">
                    <div v-for="(item,index) in indexData.balance" :key="index">
                        {{item.name}}：
                        <span v-for="(v,i) in item.children" :key="i">
                            <template v-if="v.type==1">
                                <button  @click="ShowDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==2">
                                <button  class="gold-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==3">
                                <button  class="sliver-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==4">
                                <button  @click="Drops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                        </span>
                    </div>
                </div>
            </template>
            <template v-else-if="routeName == 6">
                <div v-if="indexData">
                    <div v-for="(item,index) in indexData.event" :key="index">
                        {{item.name}}：
                        <span v-for="(v,i) in item.children" :key="i">
                            <template v-if="v.type==1">
                                <button  @click="ShowDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==2">
                                <button  class="gold-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==3">
                                <button  class="sliver-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==4">
                                <button  @click="Drops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                        </span>
                    </div>
