            </template>
            <template v-else-if="routeName == 7">
                <div v-if="indexData">
                    <div v-for="(item,index) in indexData.dragon" :key="index">
                        {{item.name}}：
                        <span v-for="(v,i) in item.children" :key="i">
                            <template v-if="v.type==1">
                                <button  @click="ShowDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==2">
                                <button  class="gold-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==3">
                                <button  class="sliver-box" @click="ClearDrops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                            <template v-else-if="v.type==4">
                                <button  @click="Drops(v.id,v.displayName)">{{v.buttonName}}</button>
                            </template>
                        </span>
                    </div>
                </div>
            </template>
            <template v-if="dropGroupId!=0 || stageclearDropId!=0">
                <item-drop-group :dropGroupId="dropGroupId" :displayName="displayName" :stageClearDropId="stageclearDropId"></item-drop-group>
            </template>
            <template v-if="dropId!=0">
                <item-drop-list :dropId="dropId"></item-drop-list>
            </template>
        </div>
    </div>
</template>

<script>
import ItemDropGroup from "@/old/items/ItemDropGroupComponent";
import ItemDropList from "@/components/item/ItemDropList.vue";
import GoBack from "@/components/util/GoBack.vue";
import DropIndex from "@/new/api/dropIndex";
export default {
    components: {
        ItemDropGroup,
        GoBack,
        ItemDropList
    },
    computed:{
        routeName(){
            switch (this.$route.name) {
                case 'item-drop-dungeon':
                    return 1;
                case 'item-drop-requestBorard':
                    return 2;
                case 'item-drop-weekDungeon':
                    return 3;
                case 'item-drop-specialDungeon':
                    return 4;
                case 'item-drop-balanceDungeon':
                    return 5;
                case 'item-drop-eventDungeon':
                    return 6;
                case 'item-drop-dragon':
                    return 7;
                default:
                    break;
            }
        }
    },
    data(){
        return{
            dropGroupId:0,
            displayName:'',
            floorArray:[],
            stageclearDropId:0,
            dropId:0,
            indexData:null,
            loading:true
        }
    },
    created(){
        this.fetchData();
    },
    methods:{
         fetchData(){
             DropIndex.getDropIndex(
                    (res) => {
                    this.indexData = res;
                    this.loading = false;
                },
                (err) => {
                    console.log(err);
                });
        },
        ShowDrops(groupId,name){
            this.dropId = 0;
            this.stageclearDropId = 0;
            this.dropGroupId = groupId;
            this.displayName = name;
        },
        ClearDrops(dropId,name){
            this.dropId = 0;
            this.dropGroupId = 0;
            this.stageclearDropId = dropId;
            this.displayName = name;
        },
        Drops(dropId,name){
            this.dropId = dropId;
            this.stageclearDropId = 0;
            this.dropGroupId = 0;
            this.displayName = name;
        }
    }
}
</script>

<style lang="less" scoped>
@import "../../less/core.less";

.item-toolkit-index {
    .kit-list {
        .kit-item {
            display: block;
            text-transform: uppercase;
            font-family: @dv-f-lato;
            font-size: 14px;

            .margin-left(0.25em);
            margin-top: 0.6em;
            
            .padding-left(0.5em);
            padding-top: 0.2em;
            padding-bottom: 0.2em;
            .border-left(@dv-c-accent-1 solid 2px);
            transition: border ease-in 0.125s, color ease-in 0.125s, background-color ease-in 0.125s;
            font-weight: normal;
            letter-spacing: 0.1em;
            color: @dv-c-accent-1;
            cursor: pointer;

            &:hover {
                color: @dv-c-foreground;
                background: fade(@dv-c-foreground, 20%);
            }
        }
        button{
            margin-left:0.6em;
            margin-top:0.6em;
        }
    }
}
.gold-box{
    background:#f5d52a;
    color:#bb4141;
}
.sliver-box{
    background:#bfbfbf;
    color:#714c4c;
}

</style>
