<template>
    <div class="itemdropgroup">
        <div v-if="loading" class="loading">
            <load-indicator
            loadText="加载基础奖励"></load-indicator>
        </div>
        <h3 v-if="this.displayName">{{this.displayName}}</h3> 
        <!-- <span>掉落组ID {{ dropGroupId }}</span> -->

        <template v-if="floors.length>0">
        <div style="margin: 10px 0">
         请选择楼层掉落
            <select ref="floorNum" @change="watchChange" v-model="floorSelected">
                <option value="" selected disabled hidden></option>
                <option v-for="(f,k) in floors" :key="k" :value ="f">{{f | floorName}}</option>
                
         </select>
        </div>
        </template>

        <div v-if="groups"  style="margin-bottom: 20px" v-show="groups[0].entries.length > 0">
            <div class="drop-title">
                <h4>基础掉落</h4>
                <img v-show="isOpenBaseDrop" src="@/assets/open.svg" alt="" @click="isOpenBaseDrop=!isOpenBaseDrop">
                <img v-show="!isOpenBaseDrop" src="@/assets/close.svg" alt="" @click="isOpenBaseDrop=!isOpenBaseDrop">
            </div>
            <collapse-transition>
            <div class="group-list" v-show="isOpenBaseDrop">
                <div class="group-entry" v-for="g in groups" :key="g.difficulty">
                    <!-- <h3 v-if="groups.length < 1">Difficulty {{g.difficulty}}</h3> -->
                    <div class="drop-entry-list">
                        <div class="drop-entry" v-for="(e, i) in g.entries" :key="i">
                            <p>
                                <span v-if="e.expectation >= 1">
                                    掉落<strong>{{Math.floor(e.expectation)}}x</strong>
                                    <span v-if="e.expectation != Math.floor(e.expectation)"> +
                                        <strong>{{e.expectation - Math.floor(e.expectation) | rate}}%</strong>
                                        概率额外掉落
                                    </span>
                                </span>
                                <span v-else>
                                    <strong>{{e.expectation | rate}}%</strong>掉落
                                </span>
                                <span class="iid">#{{e.dropId}}</span>
                                <span v-if="e.ftg == 1" class="ftg-boost">支持疲劳值燃烧效果</span>
                                <span v-if="e.PropertyDrop == 0" class="property-drop">队伍需roll</span>
                            </p>
                            <div class="item-list">
                                <div class="entry" 
                                    v-for="(e, key) in e.items" 
                                    :key="key">
                                    <item-card 
                                        :itemId="e.item.id"
                                        :itemStub="e.item"
                                        :count="e.count"
                                        :timeLimit="e.durationDays"
                                        :goldAmt="e.gold / 10000"
                                        :rate="e.rate"
                                    ></item-card>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            </collapse-transition>
        </div>
        <div>
        <div v-if="exloading" class="loading">
            <load-indicator
            loadText="层数加载"></load-indicator>
        </div>
        <div v-if="floorGroups">  
            <div class="drop-title">
                <h4>层数掉落</h4>
                <img v-show="isOpenFloorDrop" src="@/assets/open.svg" alt="" @click="isOpenFloorDrop=!isOpenFloorDrop">
                <img v-show="!isOpenFloorDrop" src="@/assets/close.svg" alt="" @click="isOpenFloorDrop=!isOpenFloorDrop">
            </div>
            <collapse-transition>
            <div class="group-list" v-show="isOpenFloorDrop">
                <div class="group-entry" v-for="g in floorGroups" :key="g.difficulty">
                    <!-- <h3 v-if="groups.length < 1">Difficulty {{g.difficulty}}</h3> -->
                    <div class="drop-entry-list">
                        <div class="drop-entry" v-for="(e, i) in g.entries" :key="i">
                            <p>
                                <b>{{e.difficulty}}</b>
                                <span v-if="e.expectation >= 1">
                                    掉落<strong>{{Math.floor(e.expectation)}}x</strong>
                                    <span v-if="e.expectation != Math.floor(e.expectation)"> +
                                        <strong>{{e.expectation - Math.floor(e.expectation) | rate}}%</strong>
                                        概率额外掉落
                                    </span>
                                </span>
                                <span v-else>
                                    <strong>{{ e.expectation | rate }}%</strong>掉落
                                </span>
                                <span class="iid">#{{e.dropId}}</span>
                                <span v-if="e.ftg == 1" class="ftg-boost">支持疲劳值燃烧效果</span>
                                <span v-if="e.PropertyDrop == 0" class="property-drop">队伍需roll</span>
                            </p>
                            <div class="item-list">
                                <div class="entry" 
                                    v-for="(e, key) in e.items" 
                                    :key="key">
                                    <item-card 
                                        :itemId="e.item.id"
                                        :itemStub="e.item"
                                        :count="e.count"
                                        :timeLimit="e.durationDays"
                                        :goldAmt="e.gold / 10000"
                                        :rate="e.rate"
                                    ></item-card>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
            </collapse-transition>
        </div>
        </div>
        <div v-if="error">
            {{ error }}
        </div>
    </div>
</template>

<script>
import Vue from 'vue';
import ItemIcon from "@/old/game/ItemIcon";
import ItemIconTooltip from "@/old/items/ItemIconTooltip";
import ItemCard from "@/old/items/ItemCard";
import ItemStat from "@/old/api/item/itemstat";
import ItemDropList from "@/components/item/ItemDropList.vue";
import Item from "@/old/api/item/item";
import ItemFilter from "@/old/api/item/itemfilter";
import collapseTransition from "@/components/util/collapse"
Vue.component('item-icon', ItemIcon);
Vue.component('item-drop-list', ItemDropList);
Vue.component('item-icon-tooltip', ItemIconTooltip);
Vue.component('item-card', ItemCard);

export default {
    props: ["dropGroupId","displayName","stageClearDropId"],
    name: "item-drop-group",
    components:{
        collapseTransition
    },
    data: function() {
        return {
            loading: true,
            groups: null,
            error: null,
            filter: ItemFilter.defaultFilter(),
            floorGroups:null,
            exDifficulty:0,
            exloading:false,
            floors:[],
            floorSelected:'',
            isOpenBaseDrop: true,
            isOpenFloorDrop: true,
            cacheArray:[]
        }
    },
    created() {
        if(this.dropGroupId!=0){
            this.fetchData();
        }
        if(this.stageClearDropId!=0){
            this.stageClear();
        }        
    },
    watch:{
        dropGroupId(newVal){
            this.isOpenBaseDrop = true;
            this.dropGroupId = newVal;
            this.floorSelected = '';
            this.exDifficulty = 0;
            this.fetchData();
        },
        stageClearDropId(newVal){
            this.isOpenBaseDrop = true;
            this.stageClearDropId = newVal;
            this.floorSelected = '';
            this.exDifficulty = 0;
            this.stageClear();
        },
    },
    filters:{
        floorName: function (value) {
            switch (value) {
                case 4:
                    return '深渊';
                case 3:
                    return '大师';
                case 2:
                    return '困难';
                case 1:
                    return '普通';
                case 0:
                    return '简单';
                default:
                    return value-4 ;
            }
        }
    },
    methods: {
        fetchData() {
            this.error = null;
            this.groups = null;
            this.floorGroups = null;        
            if (!this.dropGroupId) {
                this.loading = false;
                return;
            }
            const cacheKey = this.exDifficulty.toString() + this.dropGroupId.toString();
            this.loading = true;
            //加入缓存
            if(this.cacheArray[cacheKey]){
                this.isOpenBaseDrop = true;
                let cacheDrop = this.cacheArray[cacheKey];
                this.groups = cacheDrop;
                this.loading = false;
                this.error = null;
                this.floors = cacheDrop.floors;
                if(cacheDrop[0].entries.length == 0 && cacheDrop.floors.length>0){
                    this.selectFloor(cacheDrop.floors[0]);
                    this.floorSelected = cacheDrop.floors[0];
                }
                return;
            }
            Item.getItemDropGroup(this.dropGroupId, this.exDifficulty,this.$store.state.regionCode,
                (res) => {
                    this.groups = res;
                    this.loading = false;
                    this.error = null;
                    this.floors = res.floors;
                    this.cacheArray[cacheKey] = res;
                    if(res[0].entries.length == 0 && res.floors.length>0){
                        this.selectFloor(res.floors[0]);
                        this.floorSelected = res.floors[0];
                    }
                },
                (err) => {
                    this.loading = false;
                    this.error = err;
                });
        },
        stageClear() {
            this.error = null;
            this.groups = null;
            this.floorGroups = null;        
            if (!this.stageClearDropId) {
                this.loading = false;
                return;
            }
            const cacheKey = this.exDifficulty.toString() + this.stageClearDropId.toString();
            this.loading = true;
            //加入缓存
            if(this.cacheArray[cacheKey]){
                let cacheDrop = this.cacheArray[cacheKey];
                this.groups = cacheDrop;
                this.loading = false;
                this.error = null;
                this.floors = cacheDrop.floors;
                if(cacheDrop[0].entries.length == 0 && cacheDrop.floors.length>0){
                    this.selectFloor(cacheDrop.floors[0]);
                    this.floorSelected = cacheDrop.floors[0];
                }
                return;
            }
            Item.getStageClearDrop(this.stageClearDropId, this.exDifficulty,this.$store.state.regionCode,
                (res) => {
                    this.groups = res;
                    this.loading = false;
                    this.error = null;
                    this.floors = res.floors;
                    this.cacheArray[cacheKey] = res;
                    if(res[0].entries.length == 0 && res.floors.length>0){
                        this.selectFloor(res.floors[0]);
                        this.floorSelected = res.floors[0];
                    }
                },
                (err) => {
                    this.loading = false;
                    this.error = err;
                });
        },
        watchChange(e){
            this.selectFloor(e.target.value)
            this.isOpenBaseDrop = false;
            this.isOpenFloorDrop = true;
        },
        selectFloor(floorNum){
            if(!this.dropGroupId && !this.stageClearDropId){
                return;
            }
            this.exDifficulty = floorNum;
            this.error = null;
            this.floorGroups = null;
            if (!this.exDifficulty) {
                this.exloading = false;
                return;
            }

            if(this.dropGroupId!=0){
                this.exloading = true;
                const cacheKey = this.exDifficulty.toString() + this.dropGroupId.toString();
                //加入缓存
                if(this.cacheArray[cacheKey]){
                    let cacheDrop = this.cacheArray[cacheKey];
                    this.floorGroups = cacheDrop;
                    this.exloading = false;
                    this.error = null;
                    return;
                }
                Item.getItemDropGroup(this.dropGroupId, this.exDifficulty,this.$store.state.regionCode,
                (res) => {
                    this.floorGroups = res;
                    this.cacheArray[cacheKey] = res;
                    this.exloading = false;
                    this.error = null;
                },
                (err) => {
                    this.exloading = false;
                    this.error = err;
                });
            }else{
                this.exloading = true;
                this.groups = null;
                const cacheKey = this.exDifficulty.toString() + this.stageClearDropId.toString();
                 //加入缓存
                if(this.cacheArray[cacheKey]){
                    let cacheDrop = this.cacheArray[cacheKey];
                    this.floorGroups = cacheDrop;
                    this.exloading = false;
                    this.error = null;
                    return;
                }

                Item.getStageClearDrop(this.stageClearDropId, this.exDifficulty,this.$store.state.regionCode,
                (res) => {
                    this.floorGroups = res;
                    this.cacheArray[cacheKey] = res;
                    this.exloading = false;
                    this.error = null;
                },
                (err) => {
                    this.exloading = false;
                    this.error = err;
                });
            }

        }
    },
}
</script>

<style lang="less" scoped>
@import "../../less/core.less";

.drop-title {
    display: flex;
    justify-content: space-between;
    border: 1px #5aa9e591 solid;
    padding: 5px;
    align-items: center;
    > h4 {
        margin: 10px;
    }
    > img {
        width: 25px;
        padding: 15px;
    }
}

.group-list {
    border: 1px #5aa9e591 solid;
    border-top: none;
    padding: 5px;
}

.loading {
    margin:10px 0;
}

.itemdropgroup {

    .item-list {
        margin-top: 10px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        max-height: 500px;
        overflow-y: scroll;
        border-top: 1px solid @dv-c-accent-1;
        border-bottom: 1px solid @dv-c-accent-1;

        .result {
            &.no {
                display: none;
            }

            &:first-child.no {
                display: block;
            }
        }

        .entry {
            flex: 1 1 380px;
            border: 1px solid @dv-c-foreground;
            border-top-color: transparent;

            &:first-child {
                border-top-color: @dv-c-foreground;
            }

            @media only screen and (min-width:@min-desktop-wide-width) {
                flex: 0 1 456px;
                .border-left(none);
                &:first-child,
                &:nth-child(2) {
                    border-top-color: @dv-c-foreground;
                }
                &:nth-child(2n + 1) {
                    .border-left(1px solid @dv-c-foreground);
                }
                &:last-child {
                    // border-right: 1px solid @dv-c-foreground;
                }
                &:first-child:last-child {
                    // border-right: 1px solid @dv-c-foreground;
                }

            }
        }
    }
}

.ftg-boost{
   color:#d6d635;
   float:right;
}
.property-drop{
    color:aquamarine;
    margin-left:1.6rem;
}
.iid{
    margin: 0 6px;
    color: @dv-c-idle;
}
</style>
